.product-content {
  background: #fff;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  position: relative;
  overflow: hidden;
  margin-bottom: 20px;
  height: 210px;
}
.product-content.has-active:hover .product-title {
  transform: translateX(5px);
}
.product-content.has-active:hover .product-content-body p {
  transform: translateY(-100%);
}
.product-content.has-active:hover .product-content-footer {
