  float: left;
}
.product-content .product-content-header .product-icon img {
  transition: all .3s ease;
}
.product-content .product-content-header .product-title {
  position: relative;
  transform: translateX(0);
  transition: all .3s ease;
}
.product-content .product-content-header .product-title img {
  height: 30px;
  margin-top: 5px;
  transition: all .3s ease;
}
.product-content .product-content-header .product-title h3 {
  margin: 0;
  font-size: 18px;
  letter-spacing: 2px;
}
.product-content .product-content-header .product-title h4 {
