  font-size: 14px;
  letter-spacing: 2px;
}
.product-content .product-content-header .product-title span {
  position: absolute;
  right: 0;
  border: 1px solid #fff;
  display: inline-block;
  padding: 2px 8px;
  border-radius: 3px;
  top: 10px;
  font-size: 12px;
}
.product-content .product-content-body {
  padding: 20px 40px;
  color: #999;
  line-height: 22px;
  font-size: 13px;
  height: 110px;
  overflow: hidden;
}
.product-content .product-content-body p {
  overflow: hidden;
  height: 110px;
  margin: 0;
  transition: all .3s ease;
  transform: translateY(0);
}
.product-content .product-content-body p:last-child {
  text-align: center;
}
.product-content .product-content-body p:last-child span {
  display: inline-block;
  margin: 0 10px;
}
.product-content .product-content-body p:last-child span i {
  margin-right: 5px;
}
.product-content .product-content-footer {
  position: absolute;
  top: 100%;
  width: 100%;
  background: #69c72b;
  transition: all .3s ease;
  transform: translateY(100%);
}
.product-content .product-content-footer a {
  font-size: 16px;
  display: block;
  padding: 15px 0;
  line-height: 1;
  text-align: center;
  color: #fff;
}
.product-content .product-content-footer a i {
  margin-right: 5px;
}
