.navbar{
  transition: all .3s linear;
  position: relative;
  &.navbar-inverse:hover{
    background: darken(@navbar-inverse-bg, 8%);
  }
  .container-fluid{
    padding: 0 40px;
  }
  .navbar-right{
    li{
      padding: 15px 0;
      a{
        position: relative;
        i{
          font-size: 16px;
        }
        span{
          background: @brand-warning;
          width: 14px;
          height: 14px;
          border-radius: 50%;
          text-align: center;
          line-height: 14px;
          padding: 0;
          position: absolute;
          right: 6px;
          top: 35%;
        }
      }
    }
  }
  .nav_con{
    position: relative;
    float: left;
    margin-left: 60px;
    .line{
      height: 3px;
      background: #fff;
      position: absolute;
      left: 0;
      top: 67px;
      z-index: 9999;
      cursor: pointer;
    }
    .navbar-nav{
      li{
        a{
          padding: 14.5px 30px;
          span,
          i{
            display: block;
            text-align: center;
          }
          span{
            font-size: 16px;
            line-height: 24px;
          }
          i{
            font-size: 10px;
            font-style: normal;
            line-height: 10px;
            margin-top: 7px;
            color: rgba(255,255,255,0.8)
          }
        }
      }
    }
  }
  .nav-bg{
    background: rgba(48,57,68,0.95 );
    position: absolute;
    width: 100%;
    height: 360px;
    left: 0;
    top: 70px;
    z-index: @zindex-navbar;
    padding: 30px 0;
    display: none;
    .dropdown-list{
      position: relative;
      > li{
        position: absolute;
        display: none;
        top: 0;
        left: 0;
        width: 100%;
        .row{
          .col-xs-2{
            width: 20%;
            h5{
              color: #bababa;
              border-bottom: 1px solid #bababa;
              padding-bottom: 10px;
              line-height: 1;
              display: inline-block;
              margin-bottom: 20px;
            }
            ul{
              margin: 0;
              padding: 0;
              li{
                list-style-type: none;
                margin-bottom: 15px;
                a{
                  display: block;
                  color: #fff;
                  &:hover{
                    color: @brand-primary;
                  }
                  .proimg{
                    margin-right: 5px;
                    width: 30px;
                    display: inline-block;
                    text-align: center;
                    height: 30px;
                    img{
                      height: 20px;
                      margin-top: 8px;
                    }
                  }
                  .prolist{
                    display: inline-block;
                    font-size: 12px;
                    p{
                      margin: 0;
                      line-height: 1.1;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    ul{
      padding: 0;
      margin: 0;
      li{
        list-style-type: none;
      }
    }
    .tab-menu{
      border-right: 1px solid rgba(255,255,255,0.3);
      > li{
        list-style-type: none;
        &.active{
          a{
            background: @brand-primary;
          }
        }
        a{
          color: #fff;
          padding: 11px 15px;
          cursor: default;
          display: block;
          i{
            //float: right;
            font-size: 20px;
            margin: 0 0 0 10px;
            position: relative;
            top: 2px;
          }
        }
      }
    }
    .tab-content{
      ul{
        padding: 0;
        position: relative;
        > li{
          position: absolute;
          left: 0;
          top: 0;
          list-style-type: none;
          display: none;
          width: 100%;
          &:first-child{
            display: block;
          }
          a{
            background: rgba(255,255,255,0.2);
            padding: 15px 20px 12px 20px;
            display: block;
            margin-bottom: 20px;
            &:hover{
              background: rgba(255,255,255,0.1);
            }
            h4{
              margin: 0;
              font-size: 14px;
              font-weight: normal;
              color: #fff;
              span{
                padding: 0 4px;
                font-size: 12px;
                margin-left: 10px;
                &.v-trial{
                  color: @brand-warning;
                  border: 1px solid @brand-warning;
                }
                //&.v-Classic{
                //  color: @brand-success;
                //  border: 1px solid @brand-success;
                //}
                &.v-dev{
                  color: @brand-info;
                  border: 1px solid @brand-info;
                }
              }
            }
            p{
              margin: 3px 0 0 0;
              color: rgba(255,255,255,0.5);
              font-size: 12px;
            }
          }
        }
      }
    }
  }
}
.index-tab{
  background: #fff;
  border-bottom: 1px solid #e2e2e2;
  margin-bottom: 30px;
  .nav{
    padding: 0;
    margin: 0;
    li{
      list-style-type: none;
      float: left;
      width: 20%;
      text-align: center;
      &.active{
        a{
          color: @brand-primary;
          span{
            border-bottom: 2px solid @brand-primary;
          }
        }
      }
      a{
        display: block;
        padding: 0;
        color: #999;
        &:focus,
        &:hover{
          background: #fff;
          color: @brand-primary;
        }
        span{
          border-bottom: 2px solid transparent;
          display: inline-block;
          padding: 20px 0;
        }
      }
    }
  }
}
.index-tab-content{
  width: 1030px;
  .swiper-container{
    height: 480px;
    .swiper-pagination{
      bottom: 0;
      .swiper-pagination-bullet{
        width: 22px;
        height: 22px;
        background: #555;
        color: #fff;
        line-height: 22px;
        &.swiper-pagination-bullet-active{
          background: @brand-primary;
          color: #fff;
        }
      }
    }
  }
}
.product-content{
  background: #fff;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  position: relative;
  overflow: hidden;
  margin-bottom: 20px;
  height: 210px;
  &.has-active{
    &:hover{
      .product-title {
        transform: translateX(5px);
      }
      .product-content-body{
        p{
          transform: translateY(-100%);
        }
      }
      .product-content-footer{
          transform: translateY(-100%);
      }
      img{
        transform: scale(1.05);
      }
    }
  }
  .product-content-header{
    height: 100px;
    overflow: hidden;
    color: #fff;
    padding: 30px 40px 20px 40px;
    &.s1{
      background: @brand-primary;
    }
    &.s2{
      background: #3dc0f0;
    }
    &.s3{
      background: #ffd300;
    }
    &.s4{
      background: #f06c6e;
    }
    .product-icon{
      width: 70px;
      float: left;
      img{
        transition: all .3s ease;
      }
    }
    .product-title{
      position: relative;
      transform: translateX(0);
      transition: all .3s ease;
      img{
        height: 30px;
        margin-top: 5px;
        transition: all .3s ease;
      }
      h3{
        margin: 0;
        font-size: 18px;
        letter-spacing: 2px;
      }
      h4{
        margin: 5px 0 0 0;
        font-size: 14px;
        letter-spacing: 2px;
      }
      span{
        position: absolute;
        right: 0;
        border: 1px solid #fff;
        display: inline-block;
        padding: 2px 8px;
        border-radius: 3px;
        top: 10px;
        font-size: 12px;
      }
    }
  }
  .product-content-body{
    padding: 20px 40px;
    color: #999;
    line-height: 22px;
    font-size: 13px;
    height: 110px;
    overflow: hidden;
    p{
      overflow: hidden;
      height: 110px;
      margin: 0;
      transition: all .3s ease;
      transform: translateY(0);
      &:last-child{
        text-align: center;
        span{
          display: inline-block;
          margin: 0 10px;
          i{
            margin-right: 5px;
          }
        }
      }
    }
    //text-overflow: ellipsis;
  }
  .product-content-footer{
    //border-top: 1px solid #e9e9e9;
    position: absolute;
    top: 100%;
    width: 100%;
    //background: rgba(48,57,68,0.3);
    background: @brand-primary;
    transition: all .3s ease;
    transform: translateY(100%);
    a{
      font-size: 16px;
      display: block;
      padding: 15px 0;
      line-height: 1;
      text-align: center;
      color: #fff;
      i{
        margin-right: 5px;
      }
    }
  }
}
footer{
  padding: 30px 0 30px 0;
  //background: #404b57;
  color: rgba(255,255,255,0.8);
  font-size: 14px;
  width: 100%;
  .col-sm-3{
    a{
      color: rgba(255, 255, 255, 0.8);
    }
    padding: 0 40px;
    @media (max-width: @screen-sm-min){
      padding: 0 15px;
    }
    h3{
      color: @brand-primary;
      font-size: 14px;
      padding-bottom: 15px;
      margin: 0 0 15px 0;
      border-bottom: 1px solid @brand-primary;
    }
  }
  .copyright{
    text-align: center;
    span{
      margin-right: 15px;
    }
    i{
      color: @brand-primary;
    }
    h5{
      margin: 10px 0 20px 0;
      span{
        color: @brand-primary;
        position: relative;
        &:after,
        &:before{
          content: "";
          width: 100px;
          height: 1px;
          background: @brand-primary;
          position: absolute;
          top: 10px;
        }
        &:after{
          right: 110%;
        }
        &:before{
          left: 110%;
        }
      }
    }
    .weibo{
      margin: 15px 0;
      font-size: 0;
      a{
        display: inline-block;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        color: #fff;
        margin: 0 5px;
        &:first-child{
          background: #55c847;
        }
        &:last-child{
          background: #d2423c;
        }
        i{
          font-size: 18px;
          line-height: 30px;
          text-align: center;
          color: #fff;
        }
      }
    }
    .cpr{
      font-size: 12px;
      margin-top: 10px;
      color: #9499a6;
    }
  }
}
.login{
  background: #f4f6f8;
  .login-wrap{
    width: 800px;
    margin: 70px auto;
    .login-header{
      margin-bottom: 70px;
      img{
        width: 180px;
      }
    }
    .row{
      .col-xs-5,
      .col-xs-7{
        h4{
          font-size: 20px;
          margin-bottom: 50px;
          text-align: center;
        }
      }
      .col-xs-5{
        text-align: center;
        border-right: 1px solid #e7ebee;
        img{
          width: 170px;
          border: 1px solid #e3e8ee;
          padding: 10px;
          background: #fff;
        }
        p{
          font-size: 12px;
          margin-top: 20px;
          color: #748194;
        }
      }
      .col-xs-7{
        padding-left: 70px;
        form{
          p{
            margin: 0 0 15px 0;
            img{
              width: 40px;
              border-radius: 50%;
              margin-right: 5px;
            }
          }
          > .btn{
            padding: 9px 0;
            margin-top: 25px;
          }
          .alert{
            padding: 7px 15px;
            margin-bottom: 15px;
            display: none;
          }
          .form-group{
            overflow: hidden;
            background: #fff;
            border: 1px solid #e3e8ee;
            padding: 5px 10px;
            position: relative;
            span{
              float: left;
              display: block;
              line-height: 30px;
              //margin-right: 15px;
              width: 100px;
              color: #999;
            }
            .form-right{
              overflow: hidden;
              .code-img{
                position: absolute;
                right: 0;
                width: 100px;
                height: 40px;
                top: 0;
                img{
                  width: 100%;
                }
              }
              .btn{
                position: absolute;
                right: 0;
                top: 0;
                padding: 10px 0;
                width: 100px;
                &.btn-default{
                  background: #e3e8ee;
                  border: none;
                }
              }
              input{
                display: inline-block;
                margin: 3px 0 0 0;
                width: 100%;
                line-height: 24px;
                border: none;
                outline: none;
              }
            }
          }
        }
      }
    }
  }
}
.particles{
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}
.trial-text{
  display: inline-block;
  padding: 15px 0 0 20px;
  color: #fff;
  font-weight: normal;
  margin-left: 20px;
  position: relative;
  &:after,
  &:before{
    content: "";
    position: absolute;
    height: 30px;
    width: 1px;
    left: 0;
    top: 10px;
  }
  &:after{
    background: #60b428;
    left: -1px;
  }
  &:before{
    background: #76dc32;
  }
}
.trial_wrap{
  margin: 50px auto;
  width: 600px;
  .panel{
    margin-top: 20px;
    margin-bottom: 10px;
    .panel-body{
      padding: 10px 30px;
      .checkbox{
        margin: 10px 0;
      }
    }
  }
  button{
    margin-top: 30px;
  }
  h4{
    font-size: 24px;
    color: @brand-primary;
    text-align: center;
    margin-bottom: 40px;
  }
  .input-group{
    margin-top: 20px;
    position: relative;
    input{
      padding: 6px 50px 6px 15px;
      height: 42px;
    }
    .tip{
      position: absolute;
      right: 10px;
      top: 12px;
      color: @brand-warning;
      z-index: 10;
      //display: none;
    }
    p{
      position: absolute;
      left: 100%;
      margin-left: 10px;
      line-height: 40px;
      display: inherit;
    }
  }
}
.user-center{
  margin-bottom: 30px;
  .user-left{
    background: #fff;
    border: 1px solid #e5e5e5;
    width: 240px;
    float: left;
    margin-right: 20px;
    .user-header{
      padding: 30px;
      text-align: center;
      border-bottom: 1px solid #e5e5e5;
      img{
        width: 130px;
        border-radius: 50%;
        margin-bottom: 5px;
        //border: 1px solid #e5e5e5;
      }
      h4{
        color: #999;
        margin: 10px 0 15px 0;
      }
      .user-time{
        margin: 0 10px;
        height: 26px;
        overflow: hidden;
        border: 1px solid @brand-primary;
        i{
          background: @brand-primary;
          color: #fff;
          padding: 1px 6px;
          height: 26px;
          display: inline-block;
          float: left;
        }
        span{
          padding: 3px 10px;
          color: #999;
          height: 26px;
          display: inline-block;
        }
      }
    }
    .user-left-menu{
      margin: 10px 0 0 0;
      padding: 0;
      font-size: 16px;
      li{
        a{
          color: @gray-light;
          &:hover{
            background: #f4f9f3;
          }
        }
        &.active{
          background: #f4f9f3;
          border-right: 3px solid @brand-primary;
          border-top: 1px solid #e5e5e5;
          border-bottom: 1px solid #e5e5e5;
          a{
            color: @brand-primary;
          }
        }
      }
      > li{
        list-style-type: none;
        &.open{
          a{
            span{
              transform: rotate(0deg);
            }
          }
        }
        > a{
          display: block;
          padding: 10px 20px;
          > i{
            margin-right: 10px;
          }
          > span{
            font-size: 20px;
            float: right;
            transition: all .3s ease;
            transform: rotate(-90deg);
          }
        }
        > ul{
          padding: 0;
          margin: 0;
          > li{
            list-style-type: none;
            > a{
              display: block;
              padding: 10px 45px;
              font-size: 14px;
            }
          }
        }
      }
    }
  }
  .user-right{
    overflow: hidden;
    background: #fff;
    > h5{
      padding: 10px 30px;
    }
    .form-horizontal{
      padding: 20px 30px;
      .form-group{
        .control-label{
          text-align: left;
        }
        .radio{
          margin-top: 6px;
        }
      }
    }
    .tree-header{
      background: @brand-primary;
      color: #fff;
      padding: 10px 15px;
    }
    .tree-menu{
      padding: 20px;
      .treeview{
        width: 50%;
        max-height: 300px;
        overflow-y: scroll;
        float: left;
      }
      .tree-result{
        width: 50%;
        float: left;
        padding: 50px 30px 0 50px;
        .col-xs-6{
          border-right: 1px solid #eee;
          p{
            margin-top: 15px;
            text-align: center;
          }
          h4{
            text-align: center;
          }
        }
      }
    }
    .log-search{
      padding: 10px;
      .input-group{
        border-bottom: 1px solid #eee;
        input{
          border: none;
          padding: 5px 0 0 0;
          box-shadow: none;
          &:focus{
            box-shadow: none;
          }
        }
        span{
          a{
            color: #bdc2c7;
            font-size: 18px;
            &:hover{
              color: @brand-primary;
            }
            &:active{
              box-shadow: none;
            }
          }
        }
      }
    }
  }
  .permiss-table{
    padding: 20px;
    .toolbar{
      float: left;
    }
  }
  .manage{
    tbody{
      tr{
        td{
          img{
            height: 40px;
          }
          a{
            padding: 0 5px;
            color: #97a3b1;
            font-size: 16px;
            .fa-lock{
              color: @brand-warning;
            }
          }
        }
        //.tablemenu{
        //  position: absolute;
        //  background: rgba(151,163,177,0.8);
        //  left: 15px;
        //  right: 15px;
        //  height: 64px;
        //}
      }
    }
  }
  .tab-user{
    float: left;
    li{
      float: left;
      &.active{
        a{
          border-bottom: 3px solid @brand-primary;
          color: @brand-primary;
        }
      }
      a{
        border-bottom: 3px solid transparent;
        padding: 15px 20px;
        color: @text-color;
        &:hover,
        &:focus{
          background: #fff;
        }
      }
    }
  }
  .user-search{
    float: right;
    width: 300px;
    margin-top: 5px;
    .input-group{
      border-bottom: 1px solid #eee;
      input{
        border: none;
        padding: 5px 0 0 0;
        box-shadow: none;
        &:focus{
          box-shadow: none;
        }
      }
      span{
        a{
          color: #bdc2c7;
          font-size: 18px;
          &:hover{
            color: @brand-primary;
          }
          &:active{
            box-shadow: none;
          }
        }
      }
    }
  }
  .user-add{
    float: right;
    margin-left: 20px;
    .btn{
      padding: 16px 20px;
      border-radius: 0;
      border: none;
    }
  }
}
.set-list{
  .input-group{
    margin: 10px 0 20px 0;
  }
  .set-list-header{
    background: #97a3b1;
    color: #fff;
    margin: 0 0 10px 0;
    padding: 8px 0;
    text-align: center;
    div:first-child{
      text-align: left;
    }
  }
  .radio-scroll{
    max-height: 300px;
    overflow-y: scroll;
    overflow-x: hidden;
    .list-radio{
      &:hover{
        background: #f5f5f5;
      }
      ul{
        margin: 0;
        padding: 0;
        li{
          list-style-type: none;
          text-align: center;
          margin: 5px 0;
          line-height: 28px;
          &:first-child{
            text-align: left;
          }
          .radio{
            margin: 5px 0 0 0;
            display: inline-block;
          }
        }
      }
      .list-radio-main{
        ul{
          li{
            &:first-child{
              padding-left: 28px;
            }
            span{
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}
.report{
  position: relative;
  background: #eee;
  .report-menu{
    width: 240px;
    position: absolute;
    left: 0;
    top: 0;
    background: #eee;
    border-right: 1px solid #e5e5e5;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    transition: all .3s ease;
    > h4{
      border-bottom: 1px solid #e5e5e5;
      text-align: center;
      margin: 0;
      padding: 24px 0;
    }
    &.toggle{
      left: -240px;
    }
    > ul{
      margin: 0;
      padding: 0;
      > li{
        list-style-type: none;
        &.open{
          a{
            i{
              transform: rotate(90deg);
            }
          }
        }
        > a{
          padding: 10px 15px;
          display: block;
          color: @text-color;
          border-bottom: 1px solid #e5e5e5;
          > i{
            float: right;
            color: #aaa;
            line-height: 22px;
            transition: all .3s ease;
            font-size: 22px;
          }
        }
        > ul{
          margin: 0;
          padding: 0;
          background: #f5f5f5;
          > li{
            list-style-type: none;
            border-bottom: 1px solid #e5e5e5;
            > a{
              padding: 10px 15px 10px 35px;
              display: block;
              color: @gray-light;
              &:hover{
                background: #eee;
              }
            }
          }
        }
      }
    }
  }
  .report-wrapper{
    padding: 0 30px;
    margin-left: 240px;
    overflow: hidden;
    transition: all .3s ease;
    &.full{
      margin-left: 0;
    }
    .iframe-content{
      margin-top: 20px;
    }
  }
}
.full-screen{
  font-size: 16px;
  position: fixed;
  left: 0;
  top: 90px;
  width: 26px;
  height: 24px;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
  background: @brand-primary;
  text-align: center;
  color: #fff;
  padding: 0 3px 0 0;
  cursor: pointer;
}
.history{
  .modal-header{
    background: #69c72b url("../../public/img/background.png") no-repeat;
    background-size: cover;
    padding: 10px 15px 30px 40px;
    h4{
      text-align: center;
      img{
        width: 300px;
        margin: 30px 0 0 35px;
      }
    }
    button.close{
      color: #fff;
      opacity: 0.7;
      &:hover{
        opacity: 1;
      }
    }
  }
  .modal-body{
    form{
      padding: 30px 140px;
    }
    > p{
      color: #999;
      margin-top: 50px;
      text-align: center;
    }
  }
}
.error-page{
  text-align: center;
  margin: 100px 0;
  h5{
    color: #999;
  }
  p{
    color: #ccc;
    font-size: 12px;
  }
}